import React, { useState, useEffect } from "react";
import Dialog from "@mui/material/Dialog";
import Slide from "@mui/material/Slide";
import { API_URL } from "../../service/KEYS";

import { useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import MobileStepper from "@mui/material/MobileStepper";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import KeyboardArrowLeft from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRight from "@mui/icons-material/KeyboardArrowRight";
import SwipeableViews from "react-swipeable-views";
import { autoPlay } from "react-swipeable-views-utils";
import {Close} from "@mui/icons-material";
import "./DialogImages.css";
import parse from "html-react-parser";


const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertDialogSlide({ open,description, images, title, setOpen,setViewDialogImages }) {
  const handleClose = () => {
    setOpen(false);
    setViewDialogImages(false);
  };

  const theme = useTheme();
  const [activeStep, setActiveStep] = useState(0);
  const maxSteps = images.length;
  

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  useEffect(() => {
    const init = async() => {
      
    };

    init();
  }, []);
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        background: "aqua",
      }}
    >
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        color="secondary"
        aria-describedby="alert-dialog-slide-description"
        sx={{ display: "flex", justifyContent: "center" }}
      >
        <Paper square elevation={0}> <Button onClick={()=>{handleClose()}} ><Close/></Button></Paper>
        <Paper
          
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            pl: 2,
          }}
        >
          <Typography 
          variant="h4" 
          component="h2" 
          gutterBottom
          sx={{ color: "#616161" }}
          fontWeight="bold"
          fontFamily="unset">
            
            {title}
          </Typography>
        </Paper>
        <AutoPlaySwipeableViews
          axis={theme.direction === "rtl" ? "x-reverse" : "x"}
          index={activeStep}
          onChangeIndex={handleStepChange}
          enableMouseEvents
        >
          {images.map((step,index) => (
            <div key={index}>
              <Box
                key={step.public_name}
                component="img"
                sx={{
                  display: "block",

                  overflow: "hidden",
                  width: "100%",
                }}
                src={`${API_URL}/products/images/700/${step.public_name}`}
                alt={step.original_name}
              />
            </div>
          ))}
        </AutoPlaySwipeableViews>
        <MobileStepper
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          sx={{}}
          nextButton={
            <Button
              size="small"
              onClick={handleNext}
              disabled={activeStep === maxSteps - 1}
            >
              {theme.direction === "rtl" ? (
                <KeyboardArrowLeft />
              ) : (
                <KeyboardArrowRight />
              )}
            </Button>
          }
          backButton={
            <Button
              size="small"
              onClick={handleBack}
              disabled={activeStep === 0}
            >
              {theme.direction === "rtl" ? (
                <KeyboardArrowRight />
              ) : (
                <KeyboardArrowLeft />
              )}
            </Button>
          }
        />
        <div style={{margin:"0px 20px"}}>
        {parse(description)}
        </div>
      </Dialog>
    </div>
  );
}
